    //见积复制框自动输入检索
    querySearchEstimate(queryString, cb) {
      var estimateData = this.estimateData;
      var results = queryString
        ? estimateData.filter(this.createFilter(queryString))
        : estimateData;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    // 见积画面作业内容输入框自动检索
    querySearchWork(queryString, cb) {
      var restaurants_work = this.restaurants_work;
      var results = queryString
        ? restaurants_work.filter(this.createFilter(queryString))
        : restaurants_work;
      // 调用 callback 返回建议列表的数据
