    },
    querySearchPaymentConditions(queryString, cb) {
      var restaurants_paymentconditions = this.restaurants_paymentconditions;
      var results = queryString
        ? restaurants_paymentconditions.filter(this.createFilter(queryString))
        : restaurants_paymentconditions;
